body {
  font-family: "Signika", sans-serif;
}

.header {
  background-color: #00adef;
}

.content {
  min-height: calc(100vh - 70px - 64px);
  padding: 1.5rem 0;
}

.icon-hayao {
  margin-top: 0.4rem;
}

.footer {
  height: 400px;
  background-image: url("ghibli.jpg");
  background-position: center;
  background-size: cover;
}

.container-preloader {
  width: 200px;
  height: 60px;
  margin: 200px 500px;
  transform: translate(-50%, -50%);
}

#ball {
  width: 20px;
  height: 20px;
  position: absolute;
  left: 15%;
  animation: ball 0.5s alternate infinite ease;
}

@keyframes ball {
  0% {
    top: 60px;
    height: 5px;
  }

  40% {
    height: 20px;
  }

  100% {
    top: 0%;
  }
}

#ball:nth-child(2) {
  left: 45%;
  animation-delay: 0.2s;
}

#ball:nth-child(3) {
  left: auto;
  right: 15%;
  animation-delay: 0.3s;
}

.card {
  display: flex;
  flex-direction: column;
}

.card-content {
  flex: 1 1 auto;
}

.card-action {
  display: flex;
  justify-content: space-between;
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.btn {
  background-color: #00adef;
  font-family: "Signika", sans-serif;
  margin-top: 20px;
}

.btn:hover {
  background-color: #31d2f2;
}

.img-catalog {
  height: 300px;
}

.blob {
  overflow: hidden;
  width: 30rem;
  height: 30rem;
  border-radius: 42% 56% 72% 28% / 42% 42% 56% 48%;
  animation: morph 3.75s linear infinite;
}

@keyframes morph {
  0%,
  100% {
    border-radius: 42% 56% 72% 28% / 42% 42% 56% 48%;
  }

  33% {
    border-radius: 72% 28% 48% 48% / 28% 28% 72% 72%;
  }

  66% {
    border-radius: 100% 56% 56% 100% / 100% 100% 56% 56%;
  }
}

.icon-delete {
  width: 30px;
  margin-left: 10px;
}

.container-quotes {
  display: flex;
  justify-content: space-between;
}

.par-quotes {
  font-size: 25px;
  width: 500px;
  align-self: center;
  letter-spacing: 0.5px;
  margin-left: 10px;
}

.container-about {
  display: flex;
  justify-content: space-between;
}

.block-desc {
  align-self: center;
  font-size: 20px;
}

.par-desc {
  width: 500px;
}

.header-anime {
  display: flex;
  justify-content: center;
}

.par-fav {
  font-size: 20px;
}

.link-anime {
  background-color: #309632;
  color: #fff;
  padding: 10px;
}

.block-favAnime {
  display: flex;
}

.link-watch-favAnime {
  align-self: center;
  margin-left: 20px;
  background-color: #309632;
  color: #fff;
  padding: 10px;
}

.quantity {
  border-radius: 100%;
  background-color: #fff;
  color: #00adef;
  padding: 5px 10px;
}

.container-notice {
  display: flex;
  justify-content: center;
  letter-spacing: 0.5mm;
  padding: 0;
}

.btn-log {
  background: #fff;
  border: 1px solid #00adef;
  height: 40px;
  width: 70px;
  margin-top: 10px;
  margin-left: 10px;
  font-weight: bolder;
}

.btn-log:hover {
  background: #00adef;
}

.par-login {
  border-bottom: 2px solid #00adef;
  text-align: center;
}

.par-secret {
  font-size: 17px;
  letter-spacing: 0.4mm;
}

.user {
  color: #00adef;
}

.container-successful {
  text-align: center;
}

@media all and (max-width: 800px) {

  .container-preloader {
    margin: 200px 300px;
  }

  .img-catalog {
    height: 400px;
  }

  .container-quotes {
    flex-direction: column-reverse;
    align-items: center;
  }

  .par-quotes {
    font-size: 18px;
  }

  .container-about {
    flex-direction: column;
    align-items: center;
  }
}

@media all and (max-width: 500px) {
  .container-preloader {
    margin: 50% 45%;
  }

  .par-quotes {
    font-size: 15px;
    width: 400px;
  }

  .block-desc {
    width: 400px;
  }

  .par-desc {
    width: 400px;
    font-size: 17px;
  }

  .blob {
    width: 20rem;
    height: 20rem;
  }
}
